body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #DCDCDC;
  font: normal normal 300 1rem "Roboto", "Montserrat", sans-serif; }

.container {
  margin: 0.5rem; }

.profile-header {
  background-color: white;
  box-shadow: 1px 1px 7px 1px #bfbfbf;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: left;
          justify-content: left; }
  .profile-header > .profile-header__image {
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
    margin: 1rem; }
  .profile-header > .profile-header__title {
    margin: auto;
    min-width: 14rem; }
    .profile-header > .profile-header__title > .profile-header__title--name {
      font-weight: bold;
      font-size: 1.2rem; }
    .profile-header > .profile-header__title > .profile-header__title--job {
      color: #DCDCDC;
      font-size: 0.9rem; }
    .profile-header > .profile-header__title > .profile-header__title--media > svg {
      margin: .4rem; }

.profile-body {
  background-color: white;
  box-shadow: 1px 1px 7px 1px #bfbfbf;
  margin-top: 0.7rem;
  padding: 1rem; }
  .profile-body > .profile-body__title {
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 1rem; }
  .profile-body > .profile-body__content {
    text-align: justify;
    color: #808080; }
    .profile-body > .profile-body__content > .profile-body__content--items {
      list-style: none;
      margin: 0;
      padding: 0;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      -webkit-justify-content: center;
              justify-content: center; }
      .profile-body > .profile-body__content > .profile-body__content--items > .profile-body__content--item {
        border: 1px solid #808080;
        border-radius: 50%;
        padding: 0.4rem;
        margin: 0.2rem; }
    .profile-body > .profile-body__content > p:first-child {
      color: #000;
      color: initial; }
      .profile-body > .profile-body__content > p:first-child > .profile-body__content--job {
        font-weight: bold; }
  .profile-body hr {
    margin: 1rem 0; }

